<template>
    <div class="register-page">
        <!-- 头 -->
        <van-nav-bar title="注册" :border="false" fixed/>
        <div class="register-box">
            <div class="register-form">
            
                <div class="form-item">
                    <img class="form-item-img" src="../assets/imgs/register-phone.png" alt="">
                    <van-field v-model="register_data.user_phone" type="number" placeholder="请输入手机号" />
                </div>
                <div class="form-item">
                    <img class="form-item-img" src="../assets/imgs/register-sms.png" alt="">
                    <van-field v-model="register_data.sms_code" type="number" placeholder="请输入验证码" />
                    <div class="form-item-sms">
                        <span @click="getSms" v-if="!showTime">获取验证码</span>
                        <span @click="getSms" v-if="showTime">{{time}}</span>
                    </div>
                </div>
                <div class="form-item">
                    <img class="form-item-img" src="../assets/imgs/register-password.png" alt="">
                    <van-field v-if="password_type == '1'" v-model="register_data.password" type="password" placeholder="请输入8-16密码"/>
                    <van-field v-if="password_type == '2'" v-model="register_data.password" type="text" placeholder="请输入8-16密码"/>
                    <img class="form-item-right-img" v-if="password_type == '1'" src="../assets/imgs/register-password-1.png" alt="" @click="changePasswordType('2')">
                    <img class="form-item-right-img" v-if="password_type == '2'" src="../assets/imgs/register-password-2.png" alt="" @click="changePasswordType('1')">
                </div>
                <div class="password-tip">*必须包含数字、字母，区分大小写</div>
                <div class="form-item">
                    <img class="form-item-img" src="../assets/imgs/register-code.png" alt="">
                    <van-field v-model="register_data.invite_code" type="password" placeholder="请输入邀请码（必填）"/>
                </div>

                <div class="agreement-box">
                    <van-checkbox v-model="isagree"></van-checkbox>
                    <div class="agreement-box-text" @click="goAgreement">阅读并同意《蚪游用户协议及免责条款》</div>
                </div>

                <div class="btn" @click="register">立即注册</div>
                
            </div>
        </div>
    </div>
</template>


<script>
export default {
    data(){
        return{
            password_type:'1',//1看不见 2看的见
            isagree:true,
            register_data:{
                user_phone:'',
                sms_code:'',
                password:'',
                invite_code:''
            },
            time:60,
            showTime:false
        }
    },
    created(){

    },
    methods:{
        changePasswordType(type){
            this.password_type = type;
            
        },
        //获取验证码
        getSms(){
            if(this.register_data.user_phone == ''){
                this.utils.showToast('请输入手机号');
                return false;
            }
            if(!this.utils.regPhone(this.register_data.user_phone)){
                this.utils.showToast('请输入正确的手机号');
                return false;    
            }
            let option = {
                phone:this.register_data.user_phone,
                type:1
            }
            this.http.post(this.http._HOST + 'Sms/send_msg',option).then(result =>{
                if(result.success){

                }else{
                    this.utils.showAlert(result.message);
                }
            })
            // this.showTime = true;
            // let timeOut = setInterval(() =>{
            //     this.time--;
            //     if(this.time == 0){
            //         this.showTime = false;
            //         clearTimeout(timeOut);
            //         this.time = 60;
            //     }
            // },1000)
        },
        //查看协议
        goAgreement(){
            this.$router.push({
                name:'agreement1', 
			})	
        },
        //注册
        register(){
            if(this.register_data.user_phone == ''){
                this.utils.showToast('请输入手机号');
                return false;
            }
            if(!this.utils.regPhone(this.register_data.user_phone)){
                this.utils.showToast('请输入正确的手机号');
                return false;    
            }
            if(this.register_data.sms_code == ''){
                this.utils.showToast('请输入验证码');
                return false;
            }
            if(this.register_data.password == ''){
                this.utils.showToast('请输入密码');
                return false;
            }
            if(this.register_data.invite_code == ''){
                this.utils.showToast('请输入邀请码');
                return false;
            }

            let option = {
                user_phone:this.register_data.user_phone,
                sms_code:this.register_data.sms_code,
                password:this.register_data.password,
                invite_code:this.register_data.invite_code  
            }

            this.utils.info('注册数据...',option);
        }
    }
}
</script>



<style lang="scss" scoped>

    

    .register-page{
        padding-top:46px;
        height:100%;
        width:100%;
        position: relative;
    }
    .van-nav-bar__title{
		font-size:18px;
		
	}   
    .van-cell{
        font-size:16px;    
    }
    .van-cell:not(:last-child)::after{
        border-bottom: none;
    }
    .register-box{
        padding:0 15px;
        position: absolute;
        width:100%;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);
    }
    .form-item{
        display: flex;
        align-items: center;
        border-bottom: 1px solid #D5D5D5;
        margin-bottom: 10px;
    }
    .form-item-img{
        width:13px;
    }
    .form-item-right-img{
        width:17px;
    }
    .form-item-sms{
        width:120px;
        font-size:16px;
        color:#3D64F0;
        text-align: right;
    }
    .password-tip{
        font-size:12px;
        color:#999999;
        padding:10px 0 15px 28px;
    }
    .agreement-box{
        display: flex;
        align-items: center;    
    }
    .agreement-box{
        padding:10px 0 30px;
        display: flex;
        align-items: center;
    }
    .agreement-box-text{
        font-size:12px;
        padding-left:10px;
        color:#3D64F0;
    }
    /deep/ .van-checkbox__icon--checked .van-icon{
        border-color:#3D64F0;
        background-color:#3D64F0;
    }
    .btn{
        height:40px;
        line-height:40px;
        color:#fff;
        background-color:#3D64F0;
        font-size:18px;
        text-align: center;
        border-radius: 20px;
    }
</style>


